import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { defaultLayoutPlugin } from '@react-pdf-viewer/default-layout'; // install this library
import '@react-pdf-viewer/core/lib/styles/index.css';
import '@react-pdf-viewer/default-layout/lib/styles/index.css';
import { Viewer } from '@react-pdf-viewer/core'; 
import { Worker } from '@react-pdf-viewer/core'; // install this library
import PropagateLoader from "react-spinners/PropagateLoader";
import "./../../App.css"
   
const CoursPage = ({match,history,type, color}) => {
    const { id = "new" } = match.params;
    const [loading, setLoading] = useState(true);
    const [cours, setCours] = useState([]);
    const defaultLayoutPluginInstance = defaultLayoutPlugin();
    const fetchCours = () => {
      axios.get("http://67.211.210.18:3000/api/one_cours/" + id).then((response) => {
        setCours(response.data);
      });
    };
    useEffect(() => {
        fetchCours();
        setLoading(false)
    }, [id]);
    return ( 
        <>
        {!loading  &&  
            <div className="">
                <div className="alert alert-dismissible   shadow p-1   rounded" >
                    <nav aria-label="breadcrumb " >
                        <ol className="breadcrumb h2  " style={{ backgroundColor:'#ffff'}}>
                        <i className="fa-2x fas fa-book"></i>
                        <span className="pt-4 pl-2">Ø¯Ø±ÙˆØ³ : </span> <span className=" text-warning pt-4 pl-2" style={{maxHeight:"130px"}}> 
                          {cours.title} 
                        </span>
                        </ol>
                    </nav>
                </div>
            <div className="ml-5 mr-5" >
                <h1 className="text-info text-center" > </h1>
                <p className="text-info" > </p>
                <div className="card card-profile">
              <h4 className="text-dark text-center mt-4 ml-5 mr-5">{cours.description}</h4>
                    <div className='card p-5 pdf-container mb-4'>
                    {cours.file&&<><Worker workerUrl="https://unpkg.com/pdfjs-dist@2.6.347/build/pdf.worker.min.js">
                        <Viewer fileUrl={cours.file}
                        plugins={[defaultLayoutPluginInstance]} />
                    </Worker></>}
                    {!cours.file&&<>No pdf file selected</>}
                    </div>
              </div>
            </div>
            </div> }
            <div className="text-center mt-5  " >
             {loading &&  <PropagateLoader color={"#123abc"} loading={loading}  size={20} />}
       </div>
        </>
     );
}
 
export default CoursPage;