import { Switch } from "react-router";
import PrivateRoute from "./Authenti/PrivateRoute";
import Evaluation from "./evaluation/Evaluation";
import Exercice from "./evaluation/Exercice";

// import Evaluation from "./pages/Evaluation";

export default function Routes() {

    return (
        <div>
            <Switch> 
                <PrivateRoute  path="/app/Evaluation/:id" exact    Component={Evaluation}  />
                <PrivateRoute  path="/app/Exercice/:id"  exact  Component={Exercice}  />
            </Switch>
        </div>
    )
}
