import axios from 'axios';
import React, { useEffect, useState } from 'react';
import {  Link } from 'react-router-dom';
const Header = () => {
    const [rout, setRoutes] = useState("")
    const [module, setModule] = useState([]);
    const [cours, setCours] = useState([]);
    const fetchModule = () => {
    axios.get("http://67.211.210.18:3000/api/modules").then((response) => {
        const data = response.data;
        setModule( data);
    });
    };
    
      useEffect(() => {
        const timer = setTimeout(() => {
          fetchModule();
        }, 500);
        return () => clearTimeout(timer);
        
      }, []);
  
    useEffect(() => {
        setRoutes(window.location.pathname)
    }, [window.location.pathname]);
    return ( 
        <>
        <div className="sidebar" data-color="purple" data-background-color="white" data-image="../assets/img/sidebar-1.jpg">
            <div className="logo text-center"><Link to="/" className="" style={{width:"100%", height:"100px"}}>
                <img className="img-fluid" style={{height:"100px"}} src="./assets/img/logo-maroc.png" alt="logo maroc" />
                </Link></div>
            <div className="sidebar-wrapper">
                <ul className="nav">
                {module.length > 0 && module.map(row =>(
                    <>
                    <li className= "nav-item  active">
                        <Link className="nav-link" style={{backgroundColor:"#3a3a3a"}} to="/AllModules">
                            <i className=" colSite  fas fa-book-open pr-1"></i>
                            <p style={{fontSize:'12px'}} > {row.title.slice(0,30)}...</p>
                        </Link>
                    </li>
                    {row.cours.length > 0 && row.cours.map(cour => (
                        <>
                        <li className="nav-item ">
                            <Link className="nav-link ml-2 mb-2" to={"/cours/"+ cour.id}>
                            <span style={{fontSize:'11px'}}>{cour.title.slice(0,27)}...</span> 
                            <i className=" text-warning far fa-file-pdf"></i>
                            </Link>
                        </li>
                    
                   
                    </>
                     ))}
                </>
                  ))}




                {module.length > 0 && module.map(row =>(
                    <>
   
                    {row.cours.length > 0 && row.cours.map(cour => (
                        <>

                            {cour.evaluations.length > 0 && cour.evaluations.map( (evalua , index) => (
                                <li className="nav-item " key={index}>
                                        <Link  className="m-0 p-0" to={"/app/Exercice/"+ evalua.id} style={{fontSize:'13px',marginLeft:'50px'}}>
                                            <p className="pl-4">
                                            <i className="fas fa-user-graduate"></i>
                                                ØªÙ…Ø§Ø±ÙŠÙ† {evalua.id}</p>
                                        </Link> 
                                </li>
                            ))}
                            {cour.evaluations.length > 0 && cour.evaluations.map(evalua => (
                                <li className="nav-item ">
                                        <Link className="m-0 p-0" to={"/app/Evaluation/"+ evalua.id} style={{fontSize:'13px',marginLeft:'50px'}}>
                                            <p className="pl-4">
                                            <i className="fas fa-user-graduate"></i>
                                                {evalua.title.slice(0,30)}</p>
                                        </Link> 
                                </li>
                            ))}
                        </>
                     ))}
                    </>
                ))}

                </ul>
            </div>
            </div>
            
            
        </>
    );
}
 
export default Header;