import axios from 'axios';
import React, { useEffect, useState } from 'react'
import "./../../App.css"
import PropagateLoader from "react-spinners/PropagateLoader";
import Webcam from "react-webcam";
import Timer from 'react-compound-timer'
import Pagination from './../layout/Pagination';

const Evaluation = ({match}) => {
    const { id = "new" } = match.params;
    const [evaluation, setEvaluation] = useState([]);
    const [question, setQuestion] = useState([]);
    const [question2, setQuestion2] = useState([]);
    const [etats, setEtats] = useState({emotion :"",level : ""});
    const [resultat, setResultat] = useState(false);
    const [quiz, setQuiz] = useState({somme : 0});
    const [loading, setLoading] = useState(false);
    const [face, setFace] = useState(false);
    const [face2, setFace2] = useState(false);
    const [done, setDone] = useState(false);
    const [result, setResult] = useState({
        '1' : 0,
        '2' : 0,
        '3' : 0,
        '4' : 0,
    });
    // const [image1, setImages1] = useState({
    //     list : []
    // });
    const [image1, setImages1] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    // ------------------------------Function pour recuperer l'evaluation
    const fetchEvaluation = () => {
        setDone(v => false)
        setCurrentPage(1)
      axios.get("http://67.211.210.18:3000/api/one_evaluation/" + id).then((response) => {
        setEvaluation(response.data);
      });
      axios.get("http://67.211.210.18:3000/api/getQuestionByEvaluation/" + id).then((response) => {
        setQuestion(response.data);
        setQuestion2(response.data);
      });

        setResult({
            '0' : 0,
            '1' : 0,
            '2' : 0,
            '3' : 0
        });
        setQuiz({...quiz, somme :  0})
        setEtats({emotion :"",level : ""})
    };

    const videoConstraints = {
        width: 680,
        height: 620,
        facingMode: "user"
      };

      const webcamRef = React.useRef(null);
      const capture = () => {
            setFace(true)
            setResultat(false)
            const image2 = webcamRef.current.getScreenshot();
            const image1 = JSON.parse(window.localStorage.getItem("image1"))
            axios.post("http://67.211.210.18:3000/api/face",{image1,image2})
            .then((response) => {
              if(response.data.face === true)
              {
                  setLoading(true)
                  setResultat(false)
                  setFace2(true)
              }else{ 
                setResultat(true)
                setFace(false) 
                setLoading(false)
              }
            }); 
        }
// --------------------------------------------------fonction pour savoir l'etat d'emotion
      const   images =[]
      const emotion = () => {
            try{
                const image = webcamRef.current.getScreenshot();
                // setImages1(...image1.list.push(image) );
                setImages1(image1 => [...image1 ,image]);
            }catch{
        }
    }



    const handelgetID = (id,hello,hid , question) => {

        if(hello === id)
        {
            result[question] = 10;
            setResult(result);
        }
        else
        {
            result[question] = 0;
            setResult(result);
        }

        var final = 0;
        for( const v in result )
        {
            final += result[v];
        }
        setQuiz({...quiz, somme :  final})
        console.log("final" , final)
      };


    // ---------------------------------------recupere l'evele 
    const handlePageChange = (page) => {
        if(page > 0 && page <= pagesCount)
        setCurrentPage(page)
    
    };
    const nextQuestion = (id) => {
        // setQuestion(question.filter(ques => ques.id !== id));
        // axios.post("http://67.211.210.18:3000/api/emotion_image",{images})
        setDone(true);
        console.log("images" ,image1)
        axios.post("http://67.211.210.18:3000/api/get_level", {image1} ).then((response) => {
            setEtats(response.data)
           
        });
        // handlePageChange(filteredEvaluation[0].id)
        
        };
    //  -----------------------------------------pagination 
    const itemsPerPage = 1;
    
    // Filtrage des evaluation  en fonction de la emotion
    const filteredEvaluation = question.filter(
        c =>
          c.Niveau.toLowerCase().includes(etats.level.toLowerCase()) 
    );
    const paginatedEvaluation = Pagination.getData(
    question,
    currentPage,
    itemsPerPage
    );
    const pagesCount = Math.ceil(question.length / itemsPerPage);
    const pages = [];
        for (let i = 1; i < pagesCount; i++) {
            pages.push(i);
        }
    // ----------------------------------useEffect   get avaluation get emotion

    useEffect(() => {
        fetchEvaluation(); 
        if (loading === true){
        setInterval(() =>
        (
        emotion()
        ) ,2000);
 }
    }, [loading , id]);
    
    return ( 
        <> 
            <div className="row mt-0 pt-2 ml-2 ">
              {loading && ( <>
                <div className="col-md-9">
                    <div className="row">
                        <div className="col-md-6">
                            <div className="alert alert-dismissible   shadow p-1   rounded" >
                                <nav aria-label="breadcrumb " >
                                    <ol className="breadcrumb h3 " style={{ backgroundColor:'#ffff'}}>
                                    <i className="  fa-2x  fas fa-user-graduate"></i>  
                                    <span className=" pt-3 pl-2 text-center " style={{maxHeight:"130px"}}> 
                                        { evaluation.title}
                                    </span>
                                    </ol>
                                </nav>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="alert alert-dismissible   shadow p-1   rounded" >
                                <nav aria-label="breadcrumb" >
                                    <ol className="breadcrumb h3 " style={{ backgroundColor:'#ffff'}}>
                                    <i className=" fa-2x far fa-clock   "></i><br></br>
                                    <span className="pt-3 pl-2 text-center " style={{maxHeight:"130px"}}>
                                        <Timer className="h2" >
                                            <Timer.Hours formatValue={value => `${value} H: `} />
                                            <Timer.Minutes formatValue={value => `${value} min: `}/>
                                            <Timer.Seconds formatValue={value => `${value} s. `} />
                                        </Timer>
                                    </span>
                                    </ol>
                                </nav>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="alert alert-dismissible text-center p-1   shadow p-1   rounded" >
                                    { evaluation.description}
                            </div>
                        </div>
                    </div>
                </div>
                
                <div  className="col-md-3 ">
                    <div style={{backgroundColor:"#555"}} className="outer alert alert-dismissible    p-1   rounded" > 
                        <Webcam className="w-100"
                        audio={false}
                        ref={webcamRef}
                        screenshotFormat="image/jpeg"
                        width={300}
                        videoConstraints={videoConstraints}
                        />
                    </div>
                </div> </>
              )}
            </div>
          {!loading  && (
            <div className="container">
                <div className='row'>
                    <div className='col-md-3 mt-5'></div>
                    <div className='col-md-6 text-center'>
                        {face && <PropagateLoader  loading={true}  size={10} />}
                        {resultat  &&
                            <div className="alert alert-danger" role="alert">
                                Ù„Ø§ ÙŠÙˆØ¬Ø¯ ØªØ·Ø§Ø¨Ù‚ ÙÙŠ Ø§Ù„Ù‡ÙˆÙŠØ© Ø£Ø±Ø¬ÙˆØ§ Ø§Ø¹Ø§Ø¯Ø© Ø§Ù„ØªØ­Ù‚Ù‚ Ù…Ù† Ø§Ù„Ù‡ÙˆÙŠØ©
                            </div>
                        }
                        <h1 className="text-center text-warning mt-1 h3">ØªÙØ¹ÙŠÙ„ Ù†Ø¸Ø§Ù… Ø§Ù„ØªØ¹Ø±Ù Ø¹Ù„Ù‰ Ø§Ù„ÙˆØ¬Ù‡</h1>
                        <div className="m-2">
                        </div>
                            <div className="faceVerfier text-center mt-2" > 
                                <Webcam className="w-100"
                                    audio={false}
                                    ref={webcamRef}
                                    screenshotFormat="image/jpeg"
                                    videoConstraints={videoConstraints}
                                />
                            </div>
                            {!face && <button onClick={capture} className="text-center btn bg-dark text-light ml-5 w-50 " >
                        <i className="fas fa-qrcode mr-2"></i>ØªØ­Ù‚Ù‚ Ø§Ù„Ø§Ù†</button> }
                    </div>
                    <div className='col-md-3'>
                    </div>
                </div>
                {/* <ClimbingBoxLoader color={"#123abc"} loading={loading}  size={80} /> */}
            </div>
          )}
          {loading && !done && 
            <div  className="container mt-0">
              {paginatedEvaluation.length > 0 && paginatedEvaluation.map((evalua) => ( <>
                <div className="row">

                    <div className="col-md-4 text-center">

                    </div>
                    <div className="col-md-4 text-center">
                        <span className=" pt-3 pl-2 text-center " style={{maxHeight:"130px"}}> 

                        </span>
                    </div>
                </div>
                <div className="modal-content bg-dark mt-2">
                    <div className="container  mt-sm-5 my-1">
                        <div className="question ml-sm-5 pl-sm-5 pt-2">
                            <div className="py-2 h5 text-light mb-2">
                                <b className="mb-5">{evalua.title} : {evalua.description}</b>
                            </div>
                            <div className="ml-md-3 ml-sm-3 pl-md-5 pt-sm-0 mt-4" id="options"> 
                                <label onClick={() => handelgetID(evalua.reponse,evalua.propo1,evalua.id , currentPage)} className="options">{evalua.propo1} 
                                    <input type="radio" name="radio"/> <span className="checkmark"></span> 
                                </label> 
                                <label onClick={() => handelgetID(evalua.reponse,evalua.propo2,evalua.id , currentPage)} className="options">{evalua.propo2} 
                                    <input type="radio" name="radio"/> <span className="checkmark"></span> 
                                </label> 
                                <label onClick={() => handelgetID(evalua.reponse,evalua.propo3,evalua.id , currentPage)} className="options">{evalua.propo3} 
                                    <input type="radio" name="radio"/> <span className="checkmark"></span> 
                                </label> 
                            </div>
                        </div>
                        <div className="d-flex align-items-center pt-3">
                            <div className="row w-100">
                                <div className="col-md-4">
                                    <li className={"page-item btn bgSite  " + (currentPage === 1 && " disabled")} onClick={() => handlePageChange(currentPage - 1)}>
                                        <span className={"page-item  " + (currentPage === 1 && " disabled")} >
                                         {'<'} 
                                        </span>
                                    </li> 
                                    <li className={"btn bgSite " + (currentPage === pagesCount && " disabled")} onClick={() => handlePageChange(currentPage + 1)}>
                                        <span className={"page-item  " + (currentPage === pagesCount && " disabled")}  >
                                         {'>'} 
                                        </span>
                                    </li>
                                </div>
                                <div className="col-md-5">
                                    {itemsPerPage <= paginatedEvaluation.length && (
                                        <Pagination
                                        currentPage={currentPage}
                                        itemsPerPage={itemsPerPage}
                                        length={question.length}
                                        onPageChanged={handlePageChange}
                                        />
                                    )}
                                </div>
                                <div className="col-md-3">
                                    <span className="btn bgSite float-right " onClick={() => nextQuestion(evalua.id)}>
                                        Ø§Ø±Ø³Ø§Ù„
                                    </span>
                                </div>
                            </div>
                                <div className="ml-auto mr-sm-5 "> 
                                </div>
                            </div>
                        </div>
                    </div>
                    </>))}
                </div>
         }
         { done && loading &&  <>
                <div className="row">
                    <div className="col-md-4 text-center">

                    </div>
                    <div className="col-md-4 text-center">
                        <span className=" pt-3 pl-2 text-center " style={{maxHeight:"130px"}}> 
        
                        </span>
                    </div>
                </div>
                <div className=" pt-3 pl-2 text-center">
                    {(quiz.somme/ question.length)*10 < 50 &&
                    <div class="">
                        <i class=" text-danger fa-10x far fa-sad-tear"></i>
                        <h5 className="mt-2">Ø§Ù„Ù†ØªÙŠØ¬Ø© Ø§Ù„ØªÙŠ Ø­ØµÙ„Øª Ø¹Ù„ÙŠÙ‡Ø§Ù‡ÙŠ : <span className="text-danger ml-2"> %{(quiz.somme/question.length)*10}  </span></h5>
                    </div>
                    }
                    {(quiz.somme/question.length)*10 >= 50 &&
                        <div class="">
                            <i class="text-success fa-10x far fa-smile-beam"></i>
                            <h5 className="mt-2">Ø§Ù„Ù†ØªÙŠØ¬Ø© Ø§Ù„ØªÙŠ Ø­ØµÙ„Øª Ø¹Ù„ÙŠÙ‡Ø§Ù‡ÙŠ  : <span className="text-danger ml-2"> %{(quiz.somme/question.length)*10} </span></h5>
                        </div>
                    }
                </div>
            </>
         }
            </>
     );
}
 
export default Evaluation









