import React from "react";

// <Pagination currentPage={currentPage} itemsPerPage={itemsPerPage} length={customers.length} onPageChanged={handlePageChange} />
const Pagination = ({ currentPage, itemsPerPage, length, onPageChanged }) => {
  const pagesCount = Math.ceil(length / itemsPerPage);
  const pages = [];

  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }

  return (
    <div >
      <ul className="hello pagination justify-content-center  w-auto">
        {pages.map(page => (
          <li
            key={page}
            className={"page-item " + (currentPage === page && " bg-success")}
          >
            <button className={"page-link "+ (currentPage === page ? " bgWar" : " bg-info")} onClick={() => onPageChanged(page)}>
              
            </button>
          </li>
        ))}


      </ul>
    </div>
  );
};

Pagination.getData = (items, currentPage, itemsPerPage) => {
  const start = currentPage * itemsPerPage - itemsPerPage;
  return items.slice(start, start + itemsPerPage);
};

export default Pagination;
