import axios from "axios";
import jwtDecode from "jwt-decode";
import { LOGIN_API } from "../../config.js"

/**
 * DÃ©connexion (suppression du token du localStorage et sur Axios)
 */
function logout() {
  window.localStorage.removeItem("authToken");
  window.localStorage.removeItem("user");
  window.localStorage.removeItem("image1");
  delete axios.defaults.headers["Authorization"];
}
/**
 * RequÃªte HTTP d'authentification et stockage du token dans le storage et sur Axios
 * @param {object} credentials
 */
function authenticate(credentials) {
  return axios
    .post(LOGIN_API, credentials)
    .then(response => response)
    .then(access_token => {
      // Je stocke le token dans mon localStorage
      window.localStorage.setItem("authToken", access_token.data[0].access_token);
      window.localStorage.setItem("user", JSON.stringify(access_token.data[1].user));
      window.localStorage.setItem("image1", JSON.stringify(access_token.data[1].user.image));
      // On prÃ©vient Axios qu'on a maintenant un header par dÃ©faut sur toutes nos futures requetes HTTP
      setAxiosToken(access_token.data[0].access_token); window.location.reload()
    })
    ;
}
/**
 * Positionne le token JWT sur Axios
 * @param {string} token Le token JWT
 */
function setAxiosToken(token) {
  axios.defaults.headers["Authorization"] = "Bearer " + token;
}

/**
 * Mise en place lors du chargement de l'application
 */
function setup() {
  // 1. Voir si on a un token ?
  const token = window.localStorage.getItem("authToken");
  // 2. Si le token est encore valide
  if (token) {
    const { exp: expiration } = jwtDecode(token);
    if (expiration * 1000 > new Date().getTime()) {
      setAxiosToken(token);
    }
  }
}

/**
 * Permet de savoir si on est authentifiÃ© ou pas
 * @returns boolean
 */
function isAuthenticated() {
  // 1. Voir si on a un token ?
  const token = window.localStorage.getItem("authToken");
  // 2. Si le token est encore valide
  if (token) {
    const { exp: expiration } = jwtDecode(token);
    if (expiration * 1000 > new Date().getTime()) {
      return true;
    }
    return false;
  }
  return false;
}

const data  = {
  authenticate,
  logout,
  setup,
  isAuthenticated
};

export default data;