import HomePage from "./components/Home/HomePage";
import { Route, BrowserRouter as Router  ,Switch, Redirect } from 'react-router-dom'
import Aside from "./components/layout/Aside";
import AuthAPI from "./components/Authenti/AutoApi"
import AutuContext from "./components/Authenti/AutuContext";
import "./index.css"
import Header from "./components/layout/Header";
import AllCours from "./components/cours/AllCours";
import AllEvaluations from "./components/evaluation/AllEvaluations";
import AllModules from "./components/Modules/AllModules";
import CoursPage from "./components/cours/CoursPage";
import MainApp from "./components/MainApp";
import LoginPage from "./components/Authenti/LoginPage";
import RegisterStudents from "./components/users/RegisterEtudiant"
import { useState } from "react";
function App() {
  const [isAuthenticated, setIsAuthenticated] = useState(
    AuthAPI.isAuthenticated()
  );
  return (
    <> 
        <AutuContext.Provider
      value={{
        isAuthenticated,
        setIsAuthenticated
      }}
    > 
    <Router> 
      <Switch>
          <div className="content-wrapper">
            <div className="wrapper">
              <Header />
              <div className="main-panel">
                <Aside />
                <div className="content">
                    <Route path="/app"  component={MainApp}  />
                      <Route path="/AllCours"    exact  component={AllCours}   />
                      <Route path="/AllModules"   exact component={AllModules} />
                      <Route path="/app/Evaluation"   exact component={AllEvaluations} />
                      <Route path="/cours/:id"   exact component={CoursPage} />
                      {!isAuthenticated ? <Route path="/loginStudents"   exact component={LoginPage} />:
                       <Redirect from ="" to ="/" />
                      }
                       <Route path="/register"   exact component={RegisterStudents} />
                      <Route path="/" exact  component={HomePage} />
                </div>
              </div>
            </div>
          </div> 
      </Switch>
    </Router>
    </AutuContext.Provider>
    </>
  );
}

export default App;
